let params = {
  size: "5",
  page: 1
};
export default {
  data() {
    return {
      indexId1: 1,
      squareLiData: [], // 推荐列表
      fxListDate: [], // 关注列表
      page1: 1, // 推荐列表页码
      page2: 1, // 关注列表的页码
      loadingType1: 0,
      loadingType2: 0,
      hottalk: "",
      valueStatus: 0 // 更新页面状态 防止页面卡死
    };
  },

  created() {
    this.squareList(params);
    this.gethottalk();
  },

  onReachBottom: function() {
    // 上拉加载更多
    this.loadMoreFunc();
  },

  onPullDownRefresh () {
    this.page1 = 1
    this.page2 = 1
    if (this.indexId1 == 1) {
      this.$nextTick(() => {
        this.squareList({
          size: "5",
          page: 1
        }, true);
        this.gethottalk();
      })
    }
  },

  methods: {
    // 保存图片到相册
    send () {
			if (!uni.getStorageSync('token')) {
				this.getnav('/pages/loginSon/login')

				return false
			}
      this.getnav('/pages/squareSon/Release')
    },
		save (index) {
			if (!uni.getStorageSync('token')) {
				this.getnav('/pages/loginSon/login')

				return false
			}
			let dataName = this.indexId1 === 1 ? 'squareLiData' : 'fxListDate'
			const isDownLoad = this[dataName][index].download === 1 ? true : false
			const imgs = this[dataName][index].imgurl
			this.$loading('正在下载中')
			imgs.forEach((item, i) => {
				if (item.substr(-3).toLocaleLowerCase() === 'gif') {
					this.$toast('gif动态暂时无法下载')
					this.$hideLoading()
					return false
				}
				plus.gallery.save(item, () => {
					if (i === imgs.length - 1) {
						this.$toast('下载完成')
						this.$http.downloadpic({
							fid: this[dataName][index].id
						}).then(res => {
							if (res.status === 200) {
								if (!isDownLoad) {
									this[dataName].map(item => {
										if (item.id === this[dataName][index].id) {
                      item.download = item.download + 1
                      item.is_download = item.is_download + 1
										}

										return item
									})
								}
							}
						}).catch(err => {
							console.log(JSON.stringify(err))
						})
					}
					console.log('save success');
				}, () => {
					this.$toast('保存失败')
				})
				this.$hideLoading()
			})
		},
		like (id, flag) {
			if (!uni.getStorageSync('token')) {
				this.getnav('/pages/loginSon/login')

				return false
			}
			let dataName = this.indexId1 === 1 ? 'squareLiData' : 'fxListDate'
      this.$http.like({ fid: id }).then(res => {
        if (res.status == 200) {
					this.$toast(res.mess);
					if (res.mess === '点赞成功') {
							this[dataName].map(item => {
							if (item.id === id) {
								item.is_laud = 1
								item.laudcount = item.laudcount + 1
							}

							return item
						})
					} else {
						if (res.mess === '取消点赞') {
							this[dataName].map(item => {
								if (item.id === id) {
									item.is_laud = 0
									item.laudcount = item.laudcount - 1
								}

								return item
							})
						}
					}
        }
      })
    },
    onShare (id, index) {
			if (!uni.getStorageSync('token')) {
				this.getnav('/pages/loginSon/login')

				return false
			}
      let dataName = this.indexId1 === 1 ? 'squareLiData' : 'fxListDate'
      let currItem = this[dataName][index]
      this.$utils.share({
        summary: currItem.describe,
        href: `https://www.bmsapp.cn/index/apppage/find?id=${id}`,
        title: '欢迎体验WoSmart',
        callBack: () => {
          this.$http.sharewxpy({
            fid: id
          }).then(res => {
            if (res.status === 200) {
              this[dataName].map(item => {
                if (item.id === id && item.is_share !== 1) {
                  item.sharecount = item.sharecount + 1
                  item.is_share = 1
                }

                return item
              })
            }
          })
        }
      })
    },
    clickFollow: function(id) {
			if (!uni.getStorageSync('token')) {
				this.getnav('/pages/loginSon/login')
				return false
			}

      this.$http.getfollow({ mid: id }).then(res => {
        // console.log(res,996413)
        if (res.status == 200) {
					this.$toast(res.mess);
					if (res.mess === '关注成功') {
						this.squareLiData.map(item => {
							if (item.mid === id) {
								item.is_follow = 1
							}

							return item
						})
					} else {
						if (res.mess === '取消关注') {
							this.squareLiData.map(item => {
								if (item.mid === id) {
									item.is_follow = 0
								}

								return item
							})
						}
					}
        }
      });
    },
    tabActive: function(index) {
			if (index === 2) {
				if (!uni.getStorageSync('token')) {
				  this.getnav('/pages/loginSon/login')

					return false
				}
			}

      // console.log(123465)
      this.indexId1 = index;

      if (
        this.indexId1 == 1
      ) {
        params.page = this.page1;
        this.squareList(params);
      }

      if (
        this.indexId1 == 2
      ) {
				params.type = 1;
        params.page = this.page2;
        this.squareList(params);
      }
    },

    squareList(data, refrash) {
      if (this.indexId1 == 2) {
        data.type = 1
      } else {
        data.type = ''
      }
      if (
        (this.page1 == 1 && this.indexId1 == 1) ||
        (this.page2 == 1 && this.indexId1 == 2)
      ) {
        this.$loading();
      }
      this.$http.getsquareList(data).then(res => {
        if (res.status === 200) {
          this.$hideLoading();
          if (refrash) {
            this.squareLiData = res.data.data;
            this.loadingType1 = 0;
            return false
          }

          if (res.data.data.length == 0 && this.indexId1 == 1) {
            // console.log(12346789)
            this.loadingType1 = 2;
            return false;
          }

          if (res.data.data.length == 0 && this.indexId1 == 2) {
            this.loadingType2 = 2;
            return false;
          }

          if (this.indexId1 == 1) {
            this.squareLiData = this.squareLiData.concat(res.data.data);
            this.loadingType1 = 0;
          }
          if (this.indexId1 == 2) {
            this.fxListDate = this.fxListDate.concat(res.data.data);
            this.loadingType2 = 0;
          }
        }
        // uni.stopPullDownRefresh();
      }).catch(() => {
        // uni.stopPullDownRefresh();
      })
    },

    loadMoreFunc: function() {
      // 加载函数
      if (this.indexId1 == 1) {
        if (this.loadingType1 == 1 || this.loadingType1 == 2) {
          return false;
        }
        this.loadingType1 = 1;
        // console.log(999999)
        ++this.page1;
        params.page = this.page1;
      }
      if (this.indexId1 == 2) {
        if (this.loadingType2 == 1 || this.loadingType2 == 2) {
          return false;
        }
        this.loadingType2 = 1;
        ++this.page2;
        params.page = this.page2;
      }
      // setTimeout(() => {
			// }, 500);
			this.$nextTick(() => {
        this.squareList(params);
			})
    },
    /**
     *  预览图片
     */
    preview: function(urls, urlsId) {
      uni.previewImage({
        current: urlsId, //当前显示的 图片
        indicator: "number",
        urls: urls // 预览图片的列表
      });
    },
    gethottalk() {
      this.$http.gethottalk().then(res => {
        uni.stopPullDownRefresh();
        if (res.status == 200) {
          this.hottalk = res.data.data;
        }
      }).catch(() => {
        uni.stopPullDownRefresh();
      })
    }
  },
  
  onNavigationBarButtonTap(params) {
    this.getnav('/pages/squareSon/Release')
  }
}
